const snapToGrid = (x, y, gridSize = 20) => ({
  x: Math.round(x / gridSize) * gridSize,
  y: Math.round(y / gridSize) * gridSize,
});

const computeAbsolutePosition = (block, blocks) => {
  if (block.groupId) {
    const parent = blocks.find(b => b.id === block.groupId);
    if (parent) {
      return {
        x: parent.position.x + block.position.x,
        y: parent.position.y + block.position.y,
      };
    }
  }
  return block.position;
};

const getNextChildPosition = (parentBlock, blocks) => {
  const children = blocks.filter(child => child.groupId === parentBlock.id);
  return { x: 20, y: children.length * 80 };
};

const getDefaultBlockProps = (type) => {
  if (TRIGGER_TYPES.includes(type) && type !== "route") {
    return { customerSegment: '', demographicCriteria: '', purchaseScenario: '', triggerProductCategory: '', priority: 1, delay: 0 };
  } else if (type === "route") {
    return { nextTrue: '', nextFalse: '', priority: 1, delay: 0 };
  } else if (ACTION_TYPES.includes(type)) {
    return { orderValueThreshold: '', promoCode: '', smsMessage: '', priority: 1, delay: 0 };
  } else if (CONDITION_TYPES.includes(type)) {
    return { customCondition: '', eventDelay: '', schedule: '', geoLocation: '', timeOfDay: '', priority: 1, delay: 0 };
  } else if (type === "group") {
    return { logicMode: "AND" };
  }
  return {};
};

const generateId = () => Date.now() + Math.floor(Math.random() * 1000);

const simulateBlockCondition = (block) => Math.random() > 0.5;

/* Функция для поиска или создания группы для триггера.
   Если группа создаётся, её label устанавливается равным label первого триггера. */
const getOrCreateGroupForTrigger = (dropPosition, blocks, addBlock, trigger) => {
  const nearbyGroup = blocks.find(
    b =>
      b.type === "group" &&
      Math.abs(b.position.x - dropPosition.x) < 50 &&
      Math.abs(b.position.y - dropPosition.y) < 50
  );
  if (nearbyGroup) {
    return nearbyGroup;
  }
  const newGroup = {
    type: "group",
    label: trigger.label,
    position: dropPosition,
    id: generateId(),
    next: "",
    ...getDefaultBlockProps("group")
  };
  addBlock(newGroup);
  return newGroup;
};

const logDependency = (prevBlock, newBlock, extraMessage = "") => {
  console.log(
    `Добавлен блок "${newBlock.label}" (тип: ${newBlock.type}). Его выполнение зависит от блока "${prevBlock.label}" (тип: ${prevBlock.type}). ${extraMessage}`
  );
};

const getAllowedNextTypes = (prevType) => {
  if (TRIGGER_TYPES.includes(prevType)) {
    return CONDITION_TYPES;
  }
  if (CONDITION_TYPES.includes(prevType)) {
    return ACTION_TYPES;
  }
  if (ACTION_TYPES.includes(prevType)) {
    return CONDITION_TYPES;
  }
  if (prevType === "group") {
    return TRIGGER_TYPES;
  }
  return [];
};

/* ========================= Reducer и история ========================= */

const initialStateBlocks = {
  blocks: [],
  history: [[]],
  historyIndex: 0,
};

const pushHistory = (state, newBlocks) => {
  const newHistory = state.history.slice(0, state.historyIndex + 1);
  newHistory.push(newBlocks);
  return {
    ...state,
    blocks: newBlocks,
    history: newHistory,
    historyIndex: newHistory.length - 1,
  };
};

const blocksReducer = (state, action) => {
  switch (action.type) {
    case "ADD_BLOCK": {
      const block = action.payload;
      const snappedPosition = snapToGrid(block.position.x, block.position.y);
      const newBlock = { ...block, position: snappedPosition };
      return pushHistory(state, [...state.blocks, newBlock]);
    }
    case "UPDATE_BLOCK_POSITION": {
      const { id, position } = action.payload;
      const updatedBlocks = state.blocks.map(block =>
        block.id === id ? { ...block, position } : block
      );
      return pushHistory(state, updatedBlocks);
    }
    case "UPDATE_BLOCK_PROPERTIES": {
      const { id, newProps } = action.payload;
      const updatedBlocks = state.blocks.map(block =>
        block.id === id ? { ...block, ...newProps } : block
      );
      return pushHistory(state, updatedBlocks);
    }
    case "DELETE_BLOCK": {
      const blockToDelete = state.blocks.find(block => block.id === action.payload);
      let updatedBlocks;
      if (blockToDelete && blockToDelete.type === "group") {
        updatedBlocks = state.blocks.filter(block => block.id !== action.payload && block.groupId !== action.payload);
      } else {
        updatedBlocks = state.blocks.filter(block => block.id !== action.payload);
      }
      const cleanedBlocks = updatedBlocks.map(b => {
        let newB = { ...b };
        if (newB.next && !updatedBlocks.some(x => x.id === newB.next)) newB.next = "";
        if (newB.type === "route") {
          if (newB.nextTrue && !updatedBlocks.some(x => x.id === newB.nextTrue)) newB.nextTrue = "";
          if (newB.nextFalse && !updatedBlocks.some(x => x.id === newB.nextFalse)) newB.nextFalse = "";
        }
        return newB;
      });
      return pushHistory(state, cleanedBlocks);
    }
    case "DUPLICATE_BLOCK": {
      const blockToDuplicate = action.payload;
      const newBlock = {
        ...blockToDuplicate,
        id: generateId(),
        position: { x: blockToDuplicate.position.x + 20, y: blockToDuplicate.position.y + 20 },
      };
      return pushHistory(state, [...state.blocks, newBlock]);
    }
    case "ADD_NEXT_BLOCK": {
      const { parentId, newBlock } = action.payload;
      const updatedBlocks = state.blocks.map(block =>
        block.id === parentId ? { ...block, next: newBlock.id } : block
      );
      return pushHistory(state, [...updatedBlocks, newBlock]);
    }
    case "ADD_CHILD_BLOCK": {
      const { parentId, newBlock } = action.payload;
      const updatedBlocks = state.blocks.map(block =>
        block.id === parentId ? { ...block, children: [...(block.children || []), newBlock] } : block
      );
      return pushHistory(state, [...updatedBlocks, newBlock]);
    }
    case "UNDO": {
      if (state.historyIndex > 0) {
        const newIndex = state.historyIndex - 1;
        return { ...state, blocks: state.history[newIndex], historyIndex: newIndex };
      }
      return state;
    }
    case "REDO": {
      if (state.historyIndex < state.history.length - 1) {
        const newIndex = state.historyIndex + 1;
        return { ...state, blocks: state.history[newIndex], historyIndex: newIndex };
      }
      return state;
    }
    case "RESET":
      return { blocks: [], history: [[]], historyIndex: 0 };
    case "LOAD":
      return pushHistory(state, action.payload);
    default:
      return state;
  }
};

