const LeftMenu = ({ leftMenu, onTemplateClick }) => {
  const [search, setSearch] = useState("");
  const filterGroups = (groups) => {
    return groups.map(group => ({
      ...group,
      items: group.items.filter(item =>
        item.label.toLowerCase().includes(search.toLowerCase())
      )
    })).filter(group => group.items.length > 0);
  };

  return (
    <div className="left-menu">
      <input
        className="menu-search"
        type="text"
        placeholder="Поиск блоков..."
        title="Введите текст для поиска"
        value={search}
        onChange={e => setSearch(e.target.value)}
      />
      {Object.keys(leftMenu).map(categoryKey => {
        const groups = filterGroups(leftMenu[categoryKey]);
        if (!groups.length) return null;
        return (
          <div key={categoryKey}>
            <h3 className="category-title">
              {categoryKey.charAt(0).toUpperCase() + categoryKey.slice(1)}
            </h3>
            {groups.map((group, idx) => (
              <div key={idx} className="menu-group">
                <div className="menu-group-header" title="Нажмите, чтобы свернуть/развернуть группу">
                  <span className="menu-group-title">{group.groupTitle}</span>
                </div>
                <div className="menu-group-items">
                  {group.items.map((item, index) =>
                    item.type === "template" ? (
                      <TemplateBlock key={`${categoryKey}-${idx}-${index}`} template={item} onTemplateClick={onTemplateClick} />
                    ) : (
                      allowedInitialTriggerTypes.includes(item.type) && (
                        <SidebarBlock key={`${categoryKey}-${idx}-${index}`} type={item.type} label={item.label} />
                      )
                    )
                  )}
                </div>
              </div>
            ))}
          </div>
        );
      })}
    </div>
  );
};

const DroppedBlock = ({ block, blocks, onSelectBlock, onAddNext, onDeleteBlock, updateBlockPosition }) => {
  const [{ isDragging }, drag] = useDrag({
    type: ItemTypes.BLOCK,
    item: block,
    collect: monitor => ({ isDragging: monitor.isDragging() })
  });

  const commonStyle = {
    opacity: isDragging ? 0.5 : 1,
    transition: "all 0.2s"
  };

  const absPos = computeAbsolutePosition(block, blocks);
  const style = block.groupId
    ? { ...commonStyle, marginLeft: block.position.x, marginTop: block.position.y, position: "relative" }
    : { ...commonStyle, left: absPos.x, top: absPos.y, position: "absolute" };

  if (block.type === "group") {
    const groupChildrenRef = useRef(null);
    const [, dropGroup] = useDrop({
      accept: ItemTypes.BLOCK,
      drop: (item, monitor) => {
        if (item.groupId === block.id) {
          const clientOffset = monitor.getClientOffset();
          const groupRect = groupChildrenRef.current.getBoundingClientRect();
          const newRelPos = {
            x: clientOffset.x - groupRect.left,
            y: clientOffset.y - groupRect.top
          };
          updateBlockPosition(item.id, newRelPos);
        }
      }
    });
    return (
      <div
        ref={drag}
        id={`block-${block.id}`}
        className="dropped-block group-block"
        style={style}
        title={`Группа: ${block.label}`}
        onClick={e => { e.stopPropagation(); onSelectBlock(block); }}
      >
        <div className="group-header" style={{ display: "flex", justifyContent: "space-between", alignItems: "center" }}>
          <span>{block.label}</span>
          <button
            className="delete-button"
            title="Удалить группу"
            onClick={e => { e.stopPropagation(); onDeleteBlock(block.id); }}
          >
            &times;
          </button>
        </div>
        <div ref={dropGroup} className="group-children">
          <div className="group-triggers-header">Триггеры:</div>
          <div className="group-triggers">
            {blocks.filter(child => child.groupId === block.id).map(child => (
              <DroppedBlock
                key={child.id}
                block={child}
                blocks={blocks}
                onSelectBlock={onSelectBlock}
                onAddNext={onAddNext}
                onDeleteBlock={onDeleteBlock}
                updateBlockPosition={updateBlockPosition}
              />
            ))}
          </div>
          {blocks.filter(child => child.groupId === block.id).length === 0 && (
            <div
              className="group-placeholder"
              onClick={e => { e.stopPropagation(); onAddNext(block); }}
              title="Нажмите, чтобы добавить триггер"
            >
              + Добавьте триггер
            </div>
          )}
        </div>
        {blocks.filter(child => child.groupId === block.id).length > 0 && (
          <button
            className="add-next-button"
            title="Добавить блок в группу"
            onClick={e => { e.stopPropagation(); onAddNext(block); }}
          >
            +
          </button>
        )}
      </div>
    );
  }

  return (
    <div
      ref={drag}
      id={`block-${block.id}`}
      className="dropped-block"
      style={style}
      title={`Блок: ${block.label}`}
      onClick={e => { e.stopPropagation(); onSelectBlock(block); }}
    >
      <span className="block-icon">{emojiMapping[block.type] || "🔸"}</span>
      <span className="block-label">{block.label}</span>
      <button
        className="delete-button"
        title="Удалить блок"
        onClick={e => { e.stopPropagation(); onDeleteBlock(block.id); }}
      >
        &times;
      </button>
      <button
        className="add-next-button"
        title="Добавить следующий блок"
        onClick={e => { e.stopPropagation(); onAddNext(block); }}
      >
        +
      </button>
    </div>
  );
};

const Canvas = ({ blocks, addBlock, updateBlockPosition, onSelectBlock, onAddNext, onDeleteBlock }) => {
  const canvasRef = useRef(null);
  const [, drop] = useDrop({
    accept: ItemTypes.BLOCK,
    drop: (item, monitor) => {
      const clientOffset = monitor.getClientOffset();
      const canvasRect = canvasRef.current.getBoundingClientRect();
      const dropPosition = {
        x: clientOffset.x - canvasRect.left,
        y: clientOffset.y - canvasRect.top
      };

      if (item.fromSidebar && TRIGGER_TYPES.includes(item.type)) {
        const group = getOrCreateGroupForTrigger(dropPosition, blocks, addBlock, item);
        const newTrigger = {
          type: item.type,
          label: item.label,
          position: getNextChildPosition(group, blocks),
          id: generateId(),
          groupId: group.id,
          next: "",
          ...getDefaultBlockProps(item.type)
        };
        addBlock(newTrigger);
        console.log(`Добавлен триггер "${newTrigger.label}" в группу "${group.label}"`);
        return;
      }

      if (!item.id) {
        const newBlock = {
          ...item,
          position: dropPosition,
          id: generateId(),
          next: "",
          ...getDefaultBlockProps(item.type)
        };
        addBlock(newBlock);
      } else {
        if (item.groupId) {
          const parent = blocks.find(b => b.id === item.groupId);
          if (parent) {
            const relativePos = {
              x: dropPosition.x - parent.position.x,
              y: dropPosition.y - parent.position.y
            };
            updateBlockPosition(item.id, relativePos);
          }
        } else {
          updateBlockPosition(item.id, dropPosition);
        }
      }
    }
  });

  const absPositions = blocks.map(b => computeAbsolutePosition(b, blocks));
  const maxX = absPositions.length ? Math.max(...absPositions.map(p => p.x), 800) : 800;
  const maxY = absPositions.length ? Math.max(...absPositions.map(p => p.y), 600) : 600;
  const canvasStyle = { width: maxX + 300, height: maxY + 200 };

  useEffect(() => { window.dispatchEvent(new Event('resize')); }, [blocks]);

  const topLevelBlocks = blocks.filter(b => !b.groupId);
  return (
    <div id="canvas" ref={node => { drop(node); canvasRef.current = node; }} className="canvas" style={canvasStyle} title="Зона холста">
      {topLevelBlocks.map(block => (
        <DroppedBlock
          key={block.id}
          block={block}
          blocks={blocks}
          onSelectBlock={onSelectBlock}
          onAddNext={onAddNext}
          onDeleteBlock={onDeleteBlock}
          updateBlockPosition={updateBlockPosition}
        />
      ))}
      {blocks.map(block => {
        if (block.type === "route") {
          return (
            <React.Fragment key={`xarrow-${block.id}`}>
              {block.nextTrue && blocks.find(b => b.id === block.nextTrue) && (
                <Xarrow key={`xarrow-${block.id}-true`} start={`block-${block.id}`} end={`block-${block.nextTrue}`} color="green" strokeWidth={2} animateDrawing={false} />
              )}
              {block.nextFalse && blocks.find(b => b.id === block.nextFalse) && (
                <Xarrow key={`xarrow-${block.id}-false`} start={`block-${block.id}`} end={`block-${block.nextFalse}`} color="red" strokeWidth={2} animateDrawing={false} />
              )}
            </React.Fragment>
          );
        } else if (block.next && blocks.find(b => b.id === block.next)) {
          return (
            <Xarrow key={`xarrow-${block.id}`} start={`block-${block.id}`} end={`block-${block.next}`} color="#1e88e5" strokeWidth={2} animateDrawing={false} />
          );
        }
        return null;
      })}
    </div>
  );
};

const NextBlockModal = ({ parentBlock, options, onSelect, onCancel }) => {
  const modalTitle = parentBlock.type === "group"
    ? `Выберите триггер для группы "${parentBlock.label}"`
    : `Выберите следующий блок для "${parentBlock.label}"`;

  const groupedOptions = {
    "Триггеры": options.filter(opt => TRIGGER_TYPES.includes(opt.type)),
    "Действия": options.filter(opt => ACTION_TYPES.includes(opt.type)),
    "Условия": options.filter(opt => CONDITION_TYPES.includes(opt.type))
  };

  return (
    <Modal onClose={onCancel}>
      <div className="next-block-modal">
        <h3 className="modal-title">{modalTitle}</h3>
        {Object.entries(groupedOptions).map(([groupName, opts]) =>
          opts.length ? (
            <div key={groupName} className="option-group">
              <h4 className="group-title">{groupName}</h4>
              <div className="option-grid">
                {opts.map(option => (
                  <div key={option.type + option.label} className="option-card" onClick={() => onSelect(option)} title={`Добавить блок "${option.label}"`}>
                    <span className="option-icon">{emojiMapping[option.type] || "🔸"}</span>
                    <span className="option-label">{option.label}</span>
                  </div>
                ))}
              </div>
            </div>
          ) : null
        )}
        <div className="modal-buttons">
          <button className="cancel-button" onClick={onCancel} title="Отмена">Отмена</button>
        </div>
      </div>
    </Modal>
  );
};

const EditModal = ({ block, allBlocks, onSave, onCancel }) => {
  const { register, handleSubmit, reset } = useForm({ defaultValues: block });
  useEffect(() => { reset(block); }, [block, reset]);

  const onSubmit = data => {
    if (block.type === "route") {
      data.nextTrue = data.nextTrue ? parseInt(data.nextTrue, 10) : "";
      data.nextFalse = data.nextFalse ? parseInt(data.nextFalse, 10) : "";
    } else {
      data.next = data.next ? parseInt(data.next, 10) : "";
    }
    onSave(block.id, data);
  };

  const triggerTypes = [
    "subscription", "confirmation", "welcome", "order",
    "time-sensitive", "inactivity", "exit-intent", "page-view", "click",
    "vip-customer", "new-registration", "loyalty", "birthday", "anniversary"
  ];
  const actionTypes = [
    "send-email", "apply-discount", "bonus-action", "send-sms", "send-push",
    "send-whatsapp", "apply-free-shipping", "assign-coupon", "update-loyalty-points",
    "add-loyalty-points", "redeem-loyalty-points", "apply-loyalty-discount",
    "send-survey", "update-customer-profile"
  ];
  const conditionTypes = [
    "if-no-order", "if-abandoned-cart", "route", "if-age-greater",
    "if-age-less", "if-location", "if-purchase-frequency", "if-cart-value-exceeds",
    "if-time-of-day", "if-visit-duration", "if-webhook-response", "if-inventory-low",
    "if-loyalty-points-exceed", "if-customer-tier"
  ];

  return (
    <Modal onClose={onCancel}>
      <h3>{block.type === "group" ? "Редактирование группы" : "Редактирование блока"}</h3>
      <form onSubmit={handleSubmit(onSubmit)} className="edit-form">
        <label title="Введите название блока">
          Название:
          <input type="text" {...register("label")} />
        </label>
        {block.type === "group" && (
          <label title="Выберите логическую операцию для группы">
            Логическая операция:
            <select {...register("logicMode")}>
              <option value="AND">AND (Все должны сработать)</option>
              <option value="OR">OR (Достаточно одного)</option>
            </select>
          </label>
        )}
        {block.type === "route" ? (
          <>
            <label title="Выберите блок для истинного условия">
              Если истинно:
              <select {...register("nextTrue")}>
                <option value="">Нет</option>
                {allBlocks.filter(b => b.id !== block.id).map(b => (
                  <option key={b.id} value={b.id}>
                    {b.id} - {b.label}
                  </option>
                ))}
              </select>
            </label>
            <label title="Выберите блок для ложного условия">
              Если ложно:
              <select {...register("nextFalse")}>
                <option value="">Нет</option>
                {allBlocks.filter(b => b.id !== block.id).map(b => (
                  <option key={b.id} value={b.id}>
                    {b.id} - {b.label}
                  </option>
                ))}
              </select>
            </label>
          </>
        ) : (
          <label title="Выберите следующий блок">
            Следующий блок:
            <select {...register("next")}>
              <option value="">Нет</option>
              {allBlocks.filter(b => b.id !== block.id).map(b => (
                <option key={b.id} value={b.id}>
                  {b.id} - {b.label}
                </option>
              ))}
            </select>
          </label>
        )}
        {(triggerTypes.includes(block.type) || actionTypes.includes(block.type) || conditionTypes.includes(block.type)) && (
          <>
            <label title="Введите приоритет">
              Приоритет:
              <input type="number" {...register("priority")} />
            </label>
            <label title="Введите задержку (сек.)">
              Задержка (сек.):
              <input type="number" {...register("delay")} />
            </label>
          </>
        )}
        {triggerTypes.includes(block.type) && block.type !== "route" && (
          <>
            <label title="Введите сегмент клиентов">
              Сегмент клиентов:
              <input type="text" {...register("customerSegment")} />
            </label>
            <label title="Введите демографические критерии">
              Демографические критерии:
              <input type="text" {...register("demographicCriteria")} />
            </label>
            <label title="Введите сценарий покупки">
              Сценарий покупки:
              <input type="text" {...register("purchaseScenario")} placeholder="first, returning" />
            </label>
            <label title="Введите категорию товара">
              Категория товара:
              <input type="text" {...register("triggerProductCategory")} placeholder="Например, электроника" />
            </label>
          </>
        )}
        {actionTypes.includes(block.type) && (
          <>
            <label title="Введите порог значения заказа">
              Порог значения заказа:
              <input type="number" {...register("orderValueThreshold")} placeholder="Введите число" />
            </label>
            <label title="Введите промокод">
              Промокод:
              <input type="text" {...register("promoCode")} />
            </label>
            <label title="Введите текст SMS">
              Текст SMS-сообщения:
              <input type="text" {...register("smsMessage")} placeholder="Например, Получите скидку 15%!" />
            </label>
          </>
        )}
        {conditionTypes.includes(block.type) && block.type !== "route" && (
          <>
            <label title="Введите условие">
              Условие:
              <input type="text" {...register("customCondition")} placeholder="Например, orderValue > 1000" />
            </label>
          </>
        )}
        <div className="modal-buttons">
          <button type="submit" title="Сохранить изменения">Сохранить</button>
          <button type="button" onClick={onCancel} title="Отмена">Отмена</button>
        </div>
      </form>
    </Modal>
  );
};

/* ========================= Основное приложение ========================= */

// Для поддержки работы несколькими пользователями синхронизируем состояние через Socket.IO.
const App = () => {
  // Состояние темы: "light" или "dark"
  const [theme, setTheme] = useState("light");
  const toggleTheme = useCallback(() => {
    setTheme(prev => (prev === "light" ? "dark" : "light"));
  }, []);

  const [nextParent, setNextParent] = useState(null);
  const leftMenuDataExtended = { ...leftMenuData };

  const allOptions = useMemo(() => ([
    ...[
      { type: "subscription", label: "Подписка" },
      { type: "confirmation", label: "Подтверждение" },
      { type: "welcome", label: "Приветствие" },
      { type: "order", label: "Оформление заказа" },
      { type: "abandoned-cart", label: "Брошенная корзина" },
      { type: "review-request", label: "Запрос отзыва" },
      { type: "vip-customer", label: "VIP клиент" },
      { type: "new-registration", label: "Новая регистрация" },
      { type: "loyalty", label: "Лояльный клиент" },
      { type: "time-sensitive", label: "Событие с ограниченным временем" },
      { type: "inactivity", label: "Отсутствие активности" },
      { type: "exit-intent", label: "Выход с сайта" },
      { type: "page-view", label: "Просмотр страницы" },
      { type: "click", label: "Клик" },
      { type: "birthday", label: "День рождения" },
      { type: "anniversary", label: "Годовщина" }
    ],
    ...[
      { type: "send-email", label: "Отправить Email" },
      { type: "apply-discount", label: "Применить скидку" },
      { type: "bonus-action", label: "Начислить бонусы" },
      { type: "send-sms", label: "Отправить SMS" },
      { type: "send-push", label: "Push-уведомление" },
      { type: "send-whatsapp", label: "WhatsApp сообщение" },
      { type: "apply-free-shipping", label: "Бесплатная доставка" },
      { type: "assign-coupon", label: "Выдать купон" },
      { type: "update-loyalty-points", label: "Обновить бонусы" },
      { type: "add-loyalty-points", label: "Начислить бонусы" },
      { type: "redeem-loyalty-points", label: "Списать бонусы" },
      { type: "apply-loyalty-discount", label: "Лояльная скидка" },
      { type: "send-survey", label: "Отправить опрос" },
      { type: "update-customer-profile", label: "Обновить профиль клиента" }
    ],
    ...[
      { type: "if-no-order", label: "Если нет заказа" },
      { type: "if-abandoned-cart", label: "Брошенная корзина" },
      { type: "route", label: "Маршрут (If/Else)" },
      { type: "if-age-greater", label: "Если возраст больше" },
      { type: "if-age-less", label: "Если возраст меньше" },
      { type: "if-location", label: "По региону" },
      { type: "if-purchase-frequency", label: "Частота покупок" },
      { type: "if-cart-value-exceeds", label: "Сумма корзины" },
      { type: "if-time-of-day", label: "Время суток" },
      { type: "if-visit-duration", label: "Длительность сессии" },
      { type: "if-webhook-response", label: "Ответ вебхука" },
      { type: "if-inventory-low", label: "Низкий запас" },
      { type: "if-loyalty-points-exceed", label: "Если бонусы больше" },
      { type: "if-customer-tier", label: "Уровень клиента" },
      { type: "if-new-customer", label: "Новый клиент" },
      { type: "if-repeat-customer", label: "Повторная покупка" },
      { type: "if-first-purchase", label: "Первая покупка" },
      { type: "if-holiday", label: "Праздничный период" },
      { type: "if-subscribed", label: "Подписан на рассылку" },
      { type: "if-engaged", label: "Активный клиент" },
      { type: "if-device-mobile", label: "С мобильного устройства" },
      { type: "if-referral-source", label: "Источник перехода" },
      { type: "if-discount-used", label: "Скидка уже использована" },
      { type: "if-feedback-given", label: "Отзыв оставлен" }
    ]
  ]), []);

  const allowedOptions = useMemo(() => {
    if (!nextParent || !nextParent.type) return [];
    const allowedTypes = getAllowedNextTypes(nextParent.type);
    return allOptions.filter(opt => allowedTypes.includes(opt.type));
  }, [nextParent, allOptions]);

  const [appState, appDispatch] = useReducer(blocksReducer, initialStateBlocks);
  const { blocks, historyIndex } = appState;
  const [selectedBlock, setSelectedBlock] = useState(null);
  const [simulationResult, setSimulationResult] = useState("");
  const [simulationModalOpen, setSimulationModalOpen] = useState(false);

  // Синхронизация состояния для нескольких пользователей через Socket.IO.
  const socketRef = useRef();
  useEffect(() => {
    socketRef.current = io("http://localhost:3001");
    socketRef.current.on("state update", (data) => {
      appDispatch({ type: "LOAD", payload: data.blocks });
    });
    return () => {
      socketRef.current.disconnect();
    };
  }, []);

  useEffect(() => {
    if (socketRef.current) {
      socketRef.current.emit("state update", { blocks });
    }
  }, [blocks]);

  const updateBlockPosition = useCallback((id, position) =>
    appDispatch({ type: "UPDATE_BLOCK_POSITION", payload: { id, position } }),
    [appDispatch]
  );
    
  const updateBlockProperties = useCallback((id, newProps) => {
    appDispatch({ type: "UPDATE_BLOCK_PROPERTIES", payload: { id, newProps } });
    setSelectedBlock(null);
  }, [appDispatch]);
  
  const deleteBlock = useCallback(id => {
    appDispatch({ type: "DELETE_BLOCK", payload: id });
    setSelectedBlock(null);
  }, [appDispatch]);
  
  const duplicateBlock = useCallback(block => {
    appDispatch({ type: "DUPLICATE_BLOCK", payload: block });
  }, [appDispatch]);
  
  const addBlock = useCallback(block => {
    appDispatch({ type: "ADD_BLOCK", payload: block });
  }, [appDispatch]);
  
  const addNextBlock = useCallback((parentId, newBlock) => {
    appDispatch({ type: "ADD_NEXT_BLOCK", payload: { parentId, newBlock } });
  }, [appDispatch]);

  const handleSave = useCallback(() => {
    localStorage.setItem("triggerScenario", JSON.stringify(blocks));
    alert("Сценарий сохранен!");
  }, [blocks]);
  
  const handleLoad = useCallback(() => {
    const loaded = localStorage.getItem("triggerScenario");
    if (loaded) {
      const loadedBlocks = JSON.parse(loaded);
      appDispatch({ type: "LOAD", payload: loadedBlocks });
    } else {
      alert("Нет сохраненного сценария.");
    }
  }, [appDispatch]);
  
  const handleAddNext = useCallback(parentBlock => {
    setNextParent(parentBlock);
  }, []);
  
  const handleSelectNextBlock = useCallback(option => {
    const parentBlock = nextParent;
    let newBlock;
    if (parentBlock.type === "group") {
      newBlock = {
        type: option.type,
        label: option.label,
        position: getNextChildPosition(parentBlock, blocks),
        id: generateId(),
        groupId: parentBlock.id,
        next: "",
        ...getDefaultBlockProps(option.type)
      };
      addBlock(newBlock);
      logDependency(parentBlock, newBlock, "Новый триггер добавлен в группу.");
    } else {
      newBlock = {
        type: option.type,
        label: option.label,
        position: { x: parentBlock.position.x + 300, y: parentBlock.position.y },
        id: generateId(),
        next: "",
        ...getDefaultBlockProps(option.type)
      };
      if (parentBlock.groupId) newBlock.groupId = parentBlock.groupId;
      addNextBlock(parentBlock.id, newBlock);
      logDependency(parentBlock, newBlock, "Следующий блок добавлен после предыдущего.");
    }
    setNextParent(null);
  }, [nextParent, blocks, addBlock, addNextBlock]);
  
  const simulateChain = useCallback(() => {
    let simulationLog = "";
    const topBlocks = blocks.filter(b => !b.groupId);
    const maxSteps = 100;
    topBlocks.forEach(startBlock => {
      simulationLog += `\n=== Начало цепочки ===\n`;
      simulationLog += `Шаг 1: "${startBlock.label}" [id: ${startBlock.id}, тип: ${startBlock.type}, приоритет: ${startBlock.priority || 1}, задержка: ${startBlock.delay || 0}]\n`;
      let step = 2;
      let current = startBlock;
      const visited = new Set();
      while (current && step <= maxSteps) {
        if (visited.has(current.id)) {
          simulationLog += "\nОбнаружен цикл. Симуляция остановлена.\n";
          break;
        }
        visited.add(current.id);
        if (current.type === "group") {
          const children = blocks.filter(b => b.groupId === current.id);
          if (children.length === 0) {
            simulationLog += `\nГруппа "${current.label}" пуста. Цепочка прервана.\n`;
            break;
          }
          const childResults = children.map(child => {
            const result = simulateBlockCondition(child);
            simulationLog += `   Условие для "${child.label}": ${result}\n`;
            return result;
          });
          const groupResult = current.logicMode === "AND"
            ? childResults.every(r => r)
            : childResults.some(r => r);
          simulationLog += `\n--- Группа "${current.label}" (логика: ${current.logicMode}) ${groupResult ? "сработала" : "не сработала"} ---\n`;
          if (groupResult && current.next) {
            const nextBlock = blocks.find(b => b.id === current.next);
            if (nextBlock) {
              simulationLog += `\nПереход к блоку: "${nextBlock.label}" [id: ${nextBlock.id}]. Зависимость: группа "${current.label}" выполнена.\n`;
              current = nextBlock;
              simulationLog += `Шаг ${step}: "${current.label}" [id: ${current.id}, тип: ${current.type}, приоритет: ${current.priority || 1}, задержка: ${current.delay || 0}]\n`;
              step++;
              continue;
            }
          }
          break;
        } else if (current.type === "route") {
          simulationLog += `\n--- Блок "Маршрут": "${current.label}" [id: ${current.id}, приоритет: ${current.priority || 1}, задержка: ${current.delay || 0}]\n`;
          const conditionResult = simulateBlockCondition(current);
          simulationLog += `   Симулированное условие: ${conditionResult ? "Истина" : "Ложь"}\n`;
          const branch = conditionResult ? "true" : "false";
          simulationLog += `   Выбранная ветка: ${branch}\n`;
          const nextId = conditionResult ? current.nextTrue : current.nextFalse;
          if (nextId) {
            const nextBlock = blocks.find(b => b.id === nextId);
            if (nextBlock) {
              simulationLog += `   Переход к блоку: "${nextBlock.label}" [id: ${nextBlock.id}].\n`;
              current = nextBlock;
              simulationLog += `Шаг ${step}: "${current.label}" [id: ${current.id}, тип: ${current.type}, приоритет: ${current.priority || 1}, задержка: ${current.delay || 0}]\n`;
              step++;
              continue;
            } else {
              simulationLog += "   Целевой блок для выбранной ветки не найден\n";
              break;
            }
          } else {
            simulationLog += "   Нет заданного следующего блока для выбранной ветки\n";
            break;
          }
        } else {
          if (current.next) {
            const nextBlock = blocks.find(b => b.id === current.next);
            if (nextBlock) {
              simulationLog += `\nПереход от "${current.label}" к "${nextBlock.label}".\n`;
              current = nextBlock;
              simulationLog += `Шаг ${step}: "${current.label}" [id: ${current.id}, тип: ${current.type}, приоритет: ${current.priority || 1}, задержка: ${current.delay || 0}]\n`;
              step++;
              continue;
            } else {
              simulationLog += "\nЦелевой блок не найден\n";
              break;
            }
          } else {
            break;
          }
        }
      }
      if (step > maxSteps) simulationLog += "\nПревышено максимальное число шагов симуляции.\n";
      simulationLog += "\n=== Конец цепочки ===\n";
    });
    setSimulationResult(simulationLog);
    setSimulationModalOpen(true);
  }, [blocks]);

  return (
    <DndProvider backend={HTML5Backend}>
      <div className={`app ${theme}`}>
        <Header />
        <div className="main-container">
          <aside className="sidebar" title="Добавьте триггеры, группы или шаблоны">
            <h2>Триггеры и группы</h2>
            <LeftMenu leftMenu={leftMenuDataExtended} onTemplateClick={(template) => {
              // Дополнительная логика для шаблонов при необходимости
            }} />
          </aside>
          <main className="canvas-container">
            <div className="toolbar">
              <button onClick={() => appDispatch({ type: "UNDO" })} disabled={historyIndex === 0} title="Отменить">Отмена</button>
              <button onClick={() => appDispatch({ type: "REDO" })} title="Повтор">Повтор</button>
              <button onClick={() => appDispatch({ type: "RESET" })} title="Сбросить">Сброс</button>
              <button onClick={handleSave} title="Сохранить сценарий">Сохранить</button>
              <button onClick={handleLoad} title="Загрузить сценарий">Загрузить</button>
              <button onClick={simulateChain} title="Запустить симуляцию цепочки">Симуляция</button>
              <ThemeToggle theme={theme} toggleTheme={toggleTheme} />
              {selectedBlock && (
                <>
                  <button onClick={() => deleteBlock(selectedBlock.id)} title="Удалить выделенный блок">Удалить выделенный</button>
                  <button onClick={() => duplicateBlock(selectedBlock)} title="Дублировать выделенный блок">Дублировать выделенный</button>
                </>
              )}
            </div>
            <Canvas
              blocks={blocks}
              addBlock={addBlock}
              updateBlockPosition={updateBlockPosition}
              onSelectBlock={setSelectedBlock}
              onAddNext={handleAddNext}
              onDeleteBlock={deleteBlock}
            />
          </main>
        </div>
        {selectedBlock && (
          <EditModal
            block={selectedBlock}
            allBlocks={blocks}
            onSave={updateBlockProperties}
            onCancel={() => setSelectedBlock(null)}
          />
        )}
        {nextParent && (
          <NextBlockModal
            parentBlock={nextParent}
            options={allowedOptions}
            onSelect={handleSelectNextBlock}
            onCancel={() => setNextParent(null)}
          />
        )}
        {simulationModalOpen && (
          <SimulationModal simulationResult={simulationResult} onClose={() => setSimulationModalOpen(false)} />
        )}
        <CustomDragLayer />
      </div>
    </DndProvider>
  );
};

const CustomDragLayer = () => {
  const { item, isDragging, currentOffset } = useDragLayer(monitor => ({
    item: monitor.getItem(),
    isDragging: monitor.isDragging(),
    currentOffset: monitor.getClientOffset()
  }));
  if (!isDragging || !currentOffset || !item) return null;
  const layerStyle = {
    position: "fixed",
    pointerEvents: "none",
    zIndex: 100,
    left: 0,
    top: 0,
    transform: `translate(${currentOffset.x - 20}px, ${currentOffset.y - 20}px)`
  };
  const circleStyle = {
    width: "40px",
    height: "40px",
    borderRadius: "50%",
    backgroundColor: "#fff",
    border: "2px solid #1e88e5",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    fontSize: "24px"
  };
  return (
    <div style={layerStyle}>
      <div style={circleStyle}>
        {emojiMapping[item.type] || "🔸"}
      </div>
    </div>
  );
};

export default function Root() {
  return (
    <ErrorBoundary>
      <App />
    </ErrorBoundary>
  );
}